.checkbox-input input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}
.checkbox-input input:checked + span {
  background-image: url("../assets/Checkmark.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid #08A6E4;
}
.checkbox-input span {
  width: 24px;
  height: 24px;
  display: block;
  flex-shrink: 0;
  border: 1px solid #95989A;
  border-radius: 4px;
  margin-right: 10px;
}

header {
  height: 70px;
  width: 100%;
  box-shadow: 0 3px 6px #00000029;
}
header .header-container {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
header .header-container h1 {
  font-family: "Raleway", sans-serif;
  font-size: 32px;
  line-height: 37px;
  font-weight: 300;
  color: #08A6E4;
  padding: 16px 0;
}
@media (max-width: 1024px) {
  header .header-container h1 {
    margin-left: 10px;
  }
}
header .header-container .menu-icon {
  display: none;
}
@media (max-width: 1024px) {
  header .header-container .menu-icon {
    display: inline-block;
    cursor: pointer;
    width: 20px;
    height: 16px;
    margin-right: 15px;
    position: relative;
  }
  header .header-container .menu-icon .navicon {
    display: block;
    background-color: #08A6E4;
    width: 20px;
    height: 2px;
    position: absolute;
    top: 5px;
    transition: background 0.2s ease-out;
  }
  header .header-container .menu-icon .navicon::before, header .header-container .menu-icon .navicon::after {
    content: "";
    display: block;
    background-color: #08A6E4;
    width: 20px;
    height: 2px;
    position: absolute;
    transition: all 0.2s ease-out;
  }
  header .header-container .menu-icon .navicon::before {
    top: -5px;
  }
  header .header-container .menu-icon .navicon::after {
    top: 5px;
  }
}
@media (max-width: 245px) {
  header .header-container .menu-icon {
    display: none;
  }
}
header .header-container .menu-btn {
  display: none;
}
header .header-container .menu-btn:checked ~ .menu-icon span {
  background: transparent;
}
header .header-container .menu-btn:checked ~ .menu-icon span::before {
  transform: rotate(45deg);
  top: 0;
}
header .header-container .menu-btn:checked ~ .menu-icon span::after {
  transform: rotate(-45deg);
  top: 0;
}
header .header-container .menu-btn:checked ~ nav {
  display: block;
}
@media (max-width: 1024px) {
  header .header-container nav {
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    background: #FFFFFF;
    display: none;
    transition: all 0.5s ease-out;
    box-shadow: 0 3px 6px -3px #00000029, inset 0 3px 6px -3px #00000029;
  }
}
header .header-container nav ul {
  list-style: none;
  display: flex;
}
@media (max-width: 1024px) {
  header .header-container nav ul {
    display: block;
    margin-top: 16px;
    margin-bottom: 20px;
  }
}
header .header-container nav ul a {
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  height: 70px;
  font-family: "Open Sans", sans-serif;
  font-size: 11px;
  line-height: 15px;
  font-weight: 700;
  letter-spacing: 3.28px;
  text-align: center;
  color: #A5A5A5;
  padding: 28px 12px 27px 12px;
  margin-left: 71px;
}
@media (max-width: 1024px) {
  header .header-container nav ul a {
    width: 100%;
    margin: 0;
    height: auto;
    text-align: left;
    padding: 14px 25px;
  }
}
header .header-container nav ul a:hover {
  color: #000000;
  border-bottom: 4px solid #08A6E4;
}
@media (max-width: 1024px) {
  header .header-container nav ul a:hover {
    border: none;
  }
}

