footer {
  width: 100%;
  background-color: #F7F7F7;
  box-shadow: 0 -5px 40px #00000029;
}
footer .footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
footer .footer-container .footer-text {
  margin: 20px 0 17px 0;
}
footer .footer-container .footer-text h3 {
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-size: 26px;
  line-height: 30px;
  color: #08A6E4;
  margin-bottom: 6px;
}
footer .footer-container .footer-text p {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 11px;
  line-height: 15px;
  color: #A5A5A5;
}
footer .footer-container .footer-logo {
  display: flex;
}
footer .footer-container .footer-logo .footer-link {
  display: block;
  margin-left: 20px;
}
footer .footer-container .footer-logo .footer-link:focus {
  outline: none;
}
footer .footer-container .footer-logo .footer-link .footer-logo__img {
  width: 24px;
  height: 24px;
}